.logistics {
    color: #000000;


    &-container {
        padding: 0 60px;
    }
    &__title {
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 49px;
        color: #20305B;
        margin-left: 15px;
        margin-bottom: 47px;
    }
    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        row-gap: 24px;
        padding-bottom: 64px;
    }
    &__item {
        background: #ffffff;
        padding: 30px 50px;

        &__img {
            margin-bottom: 17px;
        }
        &__descr {
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
        }
    }
}