.news {
    padding-bottom: 75px;
    &-container {
        padding: 0px 61px;
    }
    &__items {
        display: flex;
        justify-content: space-between;
        gap: 22px;
    }
    &__item {
        padding: 29px 36px;
        background-color: #ffffff;
        &-title {
            color: #20305B;
            font-style: normal;
            font-weight: 700;
            font-size: 36px;
            line-height: 49px;
            margin-bottom: 26px;
        }
        &__block {
            margin-bottom: 23px;
            &-date {
                display: inline-block;
                font-style: normal;
                font-weight: 400;
                font-size: 22px;
                line-height: 30px;
                color: #20305B;
                margin-bottom: 9px;
            }
            &-descr {
                min-height: 129px;
                max-width: 346px;
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 25px;
                color: #000000;
                letter-spacing: 1px;
            }
            &:nth-child(2n) {
                margin-bottom: 11px;
            }
            
        }
        &__block + &__block {
            padding-top: 23px;
            border-top: 0.25px solid #000000;
        }
        &__btn {
            
            margin: 0 auto;
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            text-align: center;
            color: #ffffff;

            display: block;
            background: #33BAEC;
            box-sizing: border-box;
            border-radius: 35px;
            padding: 5px 15px;
            max-width: 238px;
        }

    }
}