.footer {
    color: #ffffff;
    background: #223259;
    &-container {
        display: flex;
        justify-content: space-between;
        padding: 50px 67px;
        gap: 100px;
    }
    &__service {
        width: 30%;
        &__title{
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            margin-bottom: 20px;
        }
        &__list {

            &__link {
                color: #C8C8C8;
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 43px;
            }
        }
    }
    &__contacts {
        max-width: 400px;
        &__title {
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            margin-bottom: 20px;
        }
        &__adress,
        &__tel,
        &__mail {
            color: #C8C8C8;
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            margin-bottom: 25px;
        }
    }
    &__search {
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        &__reference{
            display: flex;
            align-items: center;

            &__img{
                margin-right: 14px;
            }
            &__descr {
                font-style: normal;
                font-weight: 700;
                font-size: 18px;
                line-height: 25px;
            }
        }
        &__account{

            display: flex;
            align-items: center;
            &__img{
                margin-right: 14px;
            }
            &__descr {
                font-style: normal;
                font-weight: 700;
                font-size: 18px;
                line-height: 25px;
            }
        }
        &__form {
            position: relative;
        }
        &__input{
            height: 40px;
            width: 100%;
            display: block;
            padding: 0 40px 0 20px;
            box-sizing: border-box;
            border: 1px solid #b4cccf;
            background: none;
            font-size: 16px;
            color: #fff;
        }
        &__btn{
           position: absolute;
           right: 9px;
           top: calc(50% - 15px);
        }
    }
}