.header {
    background-color: #ffffff;;
    align-items: center;
    z-index: 100;
    &-container {
        position: relative;
        display: flex;
        padding: 35px 70px 30px 70px;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        padding: 15px 19px;
        z-index: 100;
        position: absolute;
        left: 24px;
        top: 24px;
        background-color: #ffffff;
        width: 169px;
        height: 219px;
        &-img {
            
            // display: none;
        }
    }
}

.nav {
    &__list {
        display: flex;
        margin-left: 150px;
    }
    &__item {
        font-family: 'Roboto',sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        .active {
            color: #33BAEC;
        }
    }
    &__link {
        
        margin-right: 35px;
        color: #000000;
    }
}

.header .hamburger {
    position: relative;
    z-index: 10050;
    width: 32px;
    height: 29px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header .hamburger .line {
    width: 100%;
    height: 3px;
    background-color: #33BAEC;
    display: block;
    transition: all 0.3s ease-in-out;
}

