.slider {
    background: url(./../../img/bg.png) no-repeat center;
    
    z-index: 99;
    background-position: center -7px;
    height: 639px;
    

    &-container {
        height: 100%;
        backdrop-filter: blur(1px);
        background: rgba(0, 0, 0, 0.48);
    }
    &__content {
        color: #ffffff;
        padding: 122px 82px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &__title {
        font-weight: 800;
        font-size: 56px;
        line-height: 66px;
        margin-bottom: 18px;
    }
    &__descr {
        font-family: 'Open Sans';
        font-style: normal;
        font-size: 18px;
        line-height: 25px;
        max-width: 936px;
        margin-bottom: 25px;
    }
    &__items{
        display: flex;
        align-items: center;
        .slider__item {
            padding-right: 35px;
            margin-bottom: 47px;
            &-num {
                font-style: normal;
                font-weight: 800;
                font-size: 56px;
                line-height: 66px;
                color: #33BAEC;
                margin-bottom: 20px;
            }            
        }
        .slider__item + .slider__item {
            padding-left: 30px;
            border-left: 1px solid #F5F5F5;
        }
    }
    &__btn {
        font-style: normal;
        font-weight: 700;
        font-size: 22px;
        line-height: 30px;
        text-align: center;
        color: inherit;

        display: inline-block;
        border: 1px solid #FFFFFF;
        box-sizing: border-box;
        border-radius: 35px;
        padding: 11px 45px;
    }
}